
















































  const COUNTSECEND = 60
  
	export default {
		data() {
			return {
				mobile: '',
        code: '',
        password: '',
        password2: '',
        hasSendCode: false,
        isClickCode: false,
        sending: false,
        countNum: COUNTSECEND,
        countTimer: null,
        submiting: false
			};
		},
    onShow() {
    	// 是否处于验证码获取倒计时状态
    	if (uni.getStorageInfoSync('forgetStamp')) {
    	  let oldtime = uni.getStorageInfoSync('forgetStamp')
    	  let nowtime = new Date().getTime()
    	  let cha = parseInt((nowtime - parseInt(oldtime)) / 1000)
    	  if (cha < COUNTSECEND) {
    	    this.sending = true
    	    this.countNum = COUNTSECEND - cha
    	    this.countTimer = setInterval(() => {
    	      this.countNum --;
    	      if (this.countNum <= 0) {
    	        clearInterval(this.countTimer)
    	        this.countNum = COUNTSECEND
    	        this.sending = false
    	        this.isClickCode = false
    	      }
    	    }, 1000)
    	    this.hasSendCode = true
    	  }
    	}
    },
    methods: {
      sendCode() {
        if (this.isClickCode) return
        if (!this.$utils.validatePhone(this.mobile)) {
          uni.showToast({
          	title: '请输入正确的手机号码',
            icon: 'none'
          })
        } else {
          this.isClickCode = true
          if (!this.hasSendCode) this.hasSendCode = true
          this.$http({
            url: this.$apis.code,
            method: 'POST',
            data: {
              'type': 1,
              'mobile': this.mobile
            },
            cb: (err, data) => {
              if (!err && data.success) {
              	this.sending = true
              	this.countTimer = setInterval(() => {
              		this.countNum --
              		if (this.countNum <= 0) {
              			clearInterval(this.countTimer)
              			this.countNum = COUNTSECEND
              			this.sending = false
              			this.isClickCode = false
              		}
              	}, 1000)
              	// 保存发送时间
                uni.setStorageSync('forgetStamp', (new Date().getTime()).toString())
              } else {
                uni.showToast({
                	title: data.message,
                  icon: 'none'
                })
              	this.sending = false
              	this.isClickCode = false
              }
            }
          })
        }
      },
      submit() {
        if (!this.$utils.validatePhone(this.mobile)) {
          uni.showToast({
          	title: '请输入正确的手机号码',
            icon: 'none'
          })
          return
        }
        if (!this.hasSendCode) {
          uni.showToast({
          	title: '验证码已失效，请重新发送',
            icon: 'none'
          })
          return
        }
        if (this.code.length <= 0) {
          uni.showToast({
          	title: '请输入验证码',
            icon: 'none'
          })
          return
        }
        if (this.password.length < 6 || this.password.length > 16) {
          uni.showToast({
          	title: '请输入大于6位数小于16位数的密码',
            icon: 'none'
          })
          return
        }
        if (this.password2 !== this.password) {
          uni.showToast({
          	title: '请再次输入相同的密码',
            icon: 'none'
          })
          return
        }
				this.$http({
				  url: this.$apis.resetPassword,
				  method: 'POST',
				  data: {
				    password: this.password,
				    mobile: this.mobile,
				    verify_code: this.code,
						repeat_password:this.password2
				  },
				  cb: (err, data) => {
				    if (!err && data.success) {
				      uni.showModal({
				      	title: '重置密码成功',
				      	content: '点击确认前往登录',
				      	showCancel: false,
				      	confirmText: '好的',
				      	success: () => {
				          uni.reLaunch({
				          	url: '/pages/login/index'
				          })
				        }
				      });
				    } else {
				      this.code = ''
				      uni.showModal({
				      	content: data.message,
				        showCancel: false
				      })
				    }
				  }
				})
      }
			
    }
	}
